.Board {
  border: black solid 0.5em;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  margin: 3em;
}

.cell {
  border: black solid 0.5em;
  height: 144px;
  width: 144px;
}

.cell:hover {
  background: lightcoral;
  cursor: pointer;
}

.stats-column {
  color: white;
  position: fixed;
  right: 1em;
  top: 1em;
}

.stats-column > div {
  margin: 0.35em;
}

.player-turn-sign > div {
  color: white;
  font-size: 1.3em;
  margin: 0.35em;
  position: fixed;
  left: 1em;
  top: 1em;
}



